const dialog = require('electron');
import { remote } from 'electron';
// import * as fs from "fs";
import path from 'path';
import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import SystemInformation from './LandingPage/SystemInformation.vue';
// import { getFilesInFolders } from '../background';

@Component({
  components: {
    SystemInformation,
  },
})
export default class LandingPage extends Vue {
  loading3: boolean = false;
  tab = null;
  loader = '';
  items = ['web', 'shopping', 'videos', 'images', 'news'];
  text =
    'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.';

  @Watch('loader')
  loaderHandler() {
    this.loading3 = !this.loading3;

    setTimeout(() => (this.loading3 = false), 3000);

    this.loader = '';
  }

  openFileDialog() {
    const isBuild = process.env.NODE_ENV === 'production';

    // const pathToDbFile = path.join(
    //   // tslint:disable-next-line
    //   isBuild ? __dirname : __static,
    //   isBuild ? '../../' : '',
    //   '../src/data.db'
    // );
    const pathToFile = path.join(
      isBuild ? __dirname : 'dist_electron/',
      './src/background.ts'
    );
    const mainProcess = remote.require(pathToFile);
    mainProcess.getFilesInFolders();
    // getFilesInFolders();
  }

  open(link: string) {
    dialog.shell.openExternal(link); // to open something in a browser
  }
}
